
const inicialState = {
    dogs:[],
    temperament:[],
    detail:[],
    allDogs:[],
}



function rootReducer (state=inicialState, action){
    switch(action.type){
        case 'GET_DOGS':
            return{
                ...state,
                dogs: action.payload,
                allDogs: action.payload
            }
        case 'GET_TEMPERAMENTS':
            return{
                ...state,
                temperament: action.payload,
            }
        case 'GET_NAME':
            return{
                ...state,
                dogs: action.payload
            }
        case 'GET_DETAIL':
            return{
                ...state,
                detail: action.payload
            }
        default:
            return state;
    }
}

export default rootReducer;